import React, { useEffect, useState } from "react";
import { Container, Row, Col, Card, CardBody, Navbar } from "reactstrap";
import { FaBox } from "react-icons/fa"; // استخدام أيقونات
import {   useHistory } from "react-router-dom";
import apiClient from "../../../../utility/services/apiClient";
 import { useConfigs } from "../../../../utility/hooks/useConfigs";
import { useTranslation } from "../../../customizedComponents/generic_page";
import NavbarComponent from "../../../../@core/layouts/components/navbar";
import { useSkin } from "../../../../utility/hooks/useSkin";
import { useNavbarColor } from "../../../../utility/hooks/useNavbarColor";
import { useNavbarType } from "../../../../utility/hooks/useNavbarType";
import classnames from "classnames";

 
const ModulesPage = () => {
  const [modules, setModules] = useState([]);
      const configs = useConfigs()

const {t} = useTranslation();
  
  const history = useHistory();
   
  // جلب البيانات من API
  useEffect(() => {
      
   }, [ ]);
  const [navbarType, setNavbarType] = useNavbarType();

  const [skin, setSkin] = useSkin(); 
    const [navbarColor, setNavbarColor] = useNavbarColor();
   
    const navbarClasses = {
      floating: "floating-nav",
      sticky: "fixed-top",
      static: "navbar-static-top",
      hidden: "d-none",
    };
  
    const bgColorCondition =
      navbarColor !== "" && navbarColor !== "light" && navbarColor !== "white";
  
  return (
    <>
       <Navbar
        expand="lg"
        light={skin !== 'dark'}
        dark={skin === 'dark' || bgColorCondition}
        color={bgColorCondition ? navbarColor : undefined}
        className={classnames(
          `header-navbar navbar align-items-center ${navbarClasses[navbarType] || "floating-nav"
          } navbar-shadow`
        )}
        style={{margin:"2% 3%", minWidth:"95%"}}
      >
        <div
          className="navbar-container d-flex content"
          style={{
            minHeight: "4.45rem",
            //  background: skin === "light" ? "white" : null
          }}
        >
            <NavbarComponent
             // setMenuVisibility={setMenuVisibility}
              skin={skin}
              setSkin={setSkin}
            />
           
        </div>
      </Navbar>
    
    <Container className="mt-4" style={{height:"90vh",paddingTop:"5%"}}>
     
      <Row>
        {modules.map((module, index) => (
          <Col lg="3" md="4" sm="6" xs="12" key={index} className="mb-3">
            <Card className="text-center shadow-sm" onClick={() => {
              localStorage.setItem("menuID",module?.id);
              localStorage.setItem("base",module?.name);
              history.push("/home")
              window.location.reload()
             }}>
              <CardBody>
                <FaBox size={40} className="mb-2" /> {/* أيقونة */}
                <h5>{t(module.name)}</h5> {/* عرض الاسم */}
              </CardBody>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
    </>
  );
};

export default ModulesPage;
